import React, { useCallback } from 'react';
import { Cascader } from 'antd';
import { DefaultOptionType } from 'antd/lib/cascader';
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import { FieldOption, LayoutMode } from '../types';

type CascaderValue = string[] | number[];

interface CascaderFieldProps {
  value?: CascaderValue;
  onChange?: (value: CascaderValue) => void;
  options?: FieldOption[];
  optionSearchable?: boolean;
  disabled?: boolean;
  layoutMode?: LayoutMode;
  clickStopPropagation?: boolean;
}

const CascaderField: React.FC<CascaderFieldProps> = ({
  onChange,
  value,
  options = [],
  disabled,
  layoutMode,
  optionSearchable = false,
  clickStopPropagation = false }: CascaderFieldProps) => {
  const filter = (inputValue: string, path: DefaultOptionType[]) => path.some(
    (option) => (option.label as string).toLowerCase().indexOf(inputValue.toLowerCase()) > -1,
  );
  const preventDefaultClick = useCallback((event) => {
    if (clickStopPropagation) {
      event.stopPropagation();
      event.preventDefault();
    }
  }, [clickStopPropagation]);
  return (
    <Cascader
      allowClear={false}
      defaultValue={value}
      suffixIcon={layoutMode === LayoutMode.COMPACT ? () => (
        <span>
          <CaretUpOutlined />
          <CaretDownOutlined />
        </span>
      ) : null}
      options={options}
      onChange={onChange}
      disabled={disabled}
      expandTrigger="click"
      placement={layoutMode === LayoutMode.COMPACT ? 'bottomRight' : 'bottomLeft'}
      showSearch={optionSearchable ? { filter } : null}
      dropdownClassName={layoutMode === LayoutMode.COMPACT ? 'easyform-cascader-dropdown-compact' : null}
      dropdownRender={(menu) => (
        <div role="button" tabIndex={0} onClick={() => {}} onKeyDown={preventDefaultClick}>
          {menu}
        </div>
      )}
    />
  );
};
export default CascaderField;
